import * as leanCloud from 'leancloud-storage/dist/av-weapp.js';
import md5 from 'md5';
export default {
	data() {
		return {
			load: false
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '登录'
		});
		uni.setNavigationBarColor({
			frontColor: '#000000',
			backgroundColor: '#ffffff'
		});
	},
	methods: {
		getUser({ detail }) {
			let _this = this;
			_this.load = true;
			console.log(detail);
			if (detail.errMsg === 'getUserInfo:ok') {
				_this.load = false;
				var queryb = new leanCloud.Query('_User');
				queryb.equalTo('nickName', detail.userInfo.nickName)
				queryb.find().then(
					function(res) {
						console.log(res)
						leanCloud.User.logIn(res[0].attributes.username, 'lamll888').then(
							function(loginedUser) {
								console.log('loginedUser');
								console.log(loginedUser);
							},
							function(error) {
								alert(JSON.stringify(error));
							}
						);
					},
					function(error) {}
				);
				
				uni.showToast({
					title: '登陆成功',
					icon: 'none',
					duration: 1000
				});
				setTimeout(function() {
					uni.navigateTo({
						url: '../index/index'
					});
				});
			} else {
				_this.load = false;
				uni.showToast({
					title: '登陆失败！请重新授权',
					icon: 'none',
					duration: 1000
				});
			}
		}
	},
	created() {}
};
